<script>
import Loader from '@/interface/template/Loader.vue';
import Subhead from '@/interface/template/outline/Subhead.vue';

export default {
  inject: ['apiServer'],
  data() {
    return {
      hostkey: this.$Key,
      activeBlock: false,
      packageId: this.$route.params.id,
      name: '',
      description: '',
      price: 0,
      duration: 30,
      limits: {
        maxAssets: 1,
        maxUsers: 5,
        storageGB: 1,
        bandwidth: 10,
        customDomain: false,
        sslCertificate: false,
        apiAccess: false,
        prioritySupport: false
      },
      features: [],
      status: true,
      loading_sources: true,
      error: null,
      success: false,
    }
  },
  components: {
    Loader,
    Subhead
  },
  methods: {
    async fetchData() {
      try {
        this.loading_sources = false;
        const response = await fetch('https://gateway.cloudrestfulapi.com/api/package/' + this.packageId, {
          method: 'GET',
          headers: { 'Content-Type': 'application/json', 'client-token-key': this.hostkey }
        });
        const data = await response.json();

        this.name = data.name;
        this.description = data.description;
        this.price = data.price;
        this.duration = data.duration;
        this.limits = data.limits || this.limits;
        this.features = data.features || [];
        this.status = data.status;

        this.loading_sources = true;
      } catch (error) {
        console.error(error);
        this.loading_sources = true;
      }
    },
    async updatePackage() {
      try {
        // Reset error state
        this.error = null;
        this.success = false;

        // Validate required fields
        if (!this.name.trim()) {
          this.error = 'กรุณาใส่ชื่อ Package';
          return;
        }

        if (!this.description.trim()) {
          this.error = 'กรุณาใส่คำอธิบาย Package';
          return;
        }

        if (this.price <= 0) {
          this.error = 'กรุณาใส่ราคาที่มากกว่า 0';
          return;
        }

        this.activeBlock = true;
        const response = await fetch('https://gateway.cloudrestfulapi.com/api/package/' + this.packageId, {
          method: 'PUT',
          headers: {
            'Content-Type': 'application/json',
            'client-token-key': this.hostkey
          },
          body: JSON.stringify({
            data: {
              name: this.name,
              description: this.description,
              price: this.price,
              duration: this.duration,
              limits: this.limits,
              features: this.features.filter(f => f.trim() !== ''), // Remove empty features
              status: this.status,
              updatedAt: new Date()
            },
            options: {
              unique: '_id'
            }
          })
        });

        const data = await response.json();
        this.activeBlock = false;
        
        if (response.ok) {
          this.success = true;
          setTimeout(() => {
            this.$router.push('/origin/package');
          }, 1500);
        } else {
          this.error = data.message || 'เกิดข้อผิดพลาดในการอัปเดต Package';
        }
      } catch (error) {
        console.error('Error updating package:', error);
        this.error = 'เกิดข้อผิดพลาดในการเชื่อมต่อกับเซิร์ฟเวอร์';
        this.activeBlock = false;
      }
    },
    addFeature() {
      this.features.push('');
    },
    removeFeature(index) {
      this.features.splice(index, 1);
    }
  },
  async mounted() {
    await this.fetchData();
  },
  setup() {
    //console.log("Setup");
  },
};
</script>

<template>
  <div v-if="!loading_sources" class="text-center">
    <Loader />
  </div>

  <template v-if="loading_sources">
    <Subhead :navigation="[
      {
        name: 'ย้อนกลับ',
        link: '/origin/package',
        style: 'chevron-left',
        class: 'secondary-btn',
        visible: true,
        type: 'button',
      },
    ]" />

    <main class="flex-1 pb-8">
      <div class="mt-8">
        <div class="flex-1 bg-gray-100">
          <div class="mt-8">
            <div class="mx-auto max-w-7xl px-6 sm:px-6 lg:px-6">
              <div class="space-y-6 sm:px-6 lg:col-span-9 lg:px-0">
                <form @submit.prevent="updatePackage">
                  <section aria-labelledby="payment-details-heading" class="relative"
                    :data-content="'กำลังติดต่อฐานข้อมูล กรุณารอสักครู่.....'"
                    :class="[(activeBlock ? 'isblock' : 'isunblock')]">

                    <div class="shadow sm:overflow-hidden sm:rounded-md">
                      <div class="bg-white py-6 px-4 sm:p-6">

                        <div>
                          <h2 id="payment-details-heading" class="text-lg font-medium leading-6 text-gray-900">
                            แก้ไข Package
                          </h2>
                          <p class="mt-1 text-sm text-gray-500">แก้ไขข้อมูล Package สำหรับ Collection</p>
                        </div>

                        <!-- Error Message -->
                        <div v-if="error" class="mt-4 rounded-md bg-red-50 p-4">
                          <div class="flex">
                            <div class="flex-shrink-0">
                              <font-awesome-icon :icon="['fas', 'exclamation-circle']" class="h-5 w-5 text-red-400" />
                            </div>
                            <div class="ml-3">
                              <h3 class="text-sm font-medium text-red-800">เกิดข้อผิดพลาด</h3>
                              <p class="mt-1 text-sm text-red-700">{{ error }}</p>
                            </div>
                          </div>
                        </div>

                        <!-- Success Message -->
                        <div v-if="success" class="mt-4 rounded-md bg-green-50 p-4">
                          <div class="flex">
                            <div class="flex-shrink-0">
                              <font-awesome-icon :icon="['fas', 'check-circle']" class="h-5 w-5 text-green-400" />
                            </div>
                            <div class="ml-3">
                              <h3 class="text-sm font-medium text-green-800">สำเร็จ</h3>
                              <p class="mt-1 text-sm text-green-700">อัปเดต Package เรียบร้อยแล้ว กำลังไปยังหน้ารายการ...</p>
                            </div>
                          </div>
                        </div>

                        <div class="mt-6 grid grid-cols-4 gap-6">
                          <div class="col-span-4 sm:col-span-2">
                            <label for="name" class="block text-sm font-medium text-gray-700">ชื่อ Package</label>
                            <input v-model="name" type="text" name="name" id="name" required
                              class="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm" />
                          </div>

                          <div class="col-span-4 sm:col-span-2">
                            <label for="price" class="block text-sm font-medium text-gray-700">ราคา (บาท)</label>
                            <input v-model="price" type="number" name="price" id="price" min="0" required
                              class="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm" />
                          </div>

                          <div class="col-span-4">
                            <label for="description" class="block text-sm font-medium text-gray-700">คำอธิบาย</label>
                            <textarea v-model="description" name="description" id="description" rows="3" required
                              class="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm"
                              placeholder="อธิบายรายละเอียดของ Package"></textarea>
                          </div>

                          <div class="col-span-4 sm:col-span-2">
                            <label for="duration" class="block text-sm font-medium text-gray-700">ระยะเวลา (วัน)</label>
                            <input v-model="duration" type="number" name="duration" id="duration" min="1" required
                              class="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm" />
                          </div>

                          <div class="col-span-4 sm:col-span-2">
                            <label class="block text-sm font-medium text-gray-700">สถานะ</label>
                            <div class="mt-1">
                              <label class="inline-flex items-center">
                                <input v-model="status" type="checkbox" class="rounded border-gray-300 text-indigo-600 shadow-sm focus:border-indigo-500 focus:ring-indigo-500">
                                <span class="ml-2 text-sm text-gray-700">เปิดใช้งาน</span>
                              </label>
                            </div>
                          </div>
                        </div>

                        <!-- Limits Section -->
                        <div class="mt-8">
                          <h3 class="text-lg font-medium text-gray-900 mb-4">ข้อจำกัดการใช้งาน</h3>
                          <div class="grid grid-cols-4 gap-6">
                            <div>
                              <label for="maxAssets" class="block text-sm font-medium text-gray-700">จำนวน Assets สูงสุด</label>
                              <input v-model="limits.maxAssets" type="number" name="maxAssets" id="maxAssets" min="1"
                                class="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm" />
                            </div>

                            <div>
                              <label for="maxUsers" class="block text-sm font-medium text-gray-700">จำนวนผู้ใช้สูงสุด</label>
                              <input v-model="limits.maxUsers" type="number" name="maxUsers" id="maxUsers" min="1"
                                class="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm" />
                            </div>

                            <div>
                              <label for="storageGB" class="block text-sm font-medium text-gray-700">พื้นที่จัดเก็บ (GB)</label>
                              <input v-model="limits.storageGB" type="number" name="storageGB" id="storageGB" min="1"
                                class="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm" />
                            </div>

                            <div>
                              <label for="bandwidth" class="block text-sm font-medium text-gray-700">Bandwidth (GB/เดือน)</label>
                              <input v-model="limits.bandwidth" type="number" name="bandwidth" id="bandwidth" min="1"
                                class="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm" />
                            </div>
                          </div>

                          <!-- Feature Toggles -->
                          <div class="mt-6 grid grid-cols-2 gap-6">
                            <div>
                              <label class="inline-flex items-center">
                                <input v-model="limits.customDomain" type="checkbox" class="rounded border-gray-300 text-indigo-600 shadow-sm focus:border-indigo-500 focus:ring-indigo-500">
                                <span class="ml-2 text-sm text-gray-700">Custom Domain</span>
                              </label>
                            </div>

                            <div>
                              <label class="inline-flex items-center">
                                <input v-model="limits.sslCertificate" type="checkbox" class="rounded border-gray-300 text-indigo-600 shadow-sm focus:border-indigo-500 focus:ring-indigo-500">
                                <span class="ml-2 text-sm text-gray-700">SSL Certificate</span>
                              </label>
                            </div>

                            <div>
                              <label class="inline-flex items-center">
                                <input v-model="limits.apiAccess" type="checkbox" class="rounded border-gray-300 text-indigo-600 shadow-sm focus:border-indigo-500 focus:ring-indigo-500">
                                <span class="ml-2 text-sm text-gray-700">API Access</span>
                              </label>
                            </div>

                            <div>
                              <label class="inline-flex items-center">
                                <input v-model="limits.prioritySupport" type="checkbox" class="rounded border-gray-300 text-indigo-600 shadow-sm focus:border-indigo-500 focus:ring-indigo-500">
                                <span class="ml-2 text-sm text-gray-700">Priority Support</span>
                              </label>
                            </div>
                          </div>
                        </div>

                        <!-- Features Section -->
                        <div class="mt-8">
                          <div class="flex justify-between items-center mb-4">
                            <h3 class="text-lg font-medium text-gray-900">คุณสมบัติเพิ่มเติม</h3>
                            <button type="button" @click="addFeature"
                              class="inline-flex items-center px-3 py-2 border border-transparent text-sm leading-4 font-medium rounded-md text-indigo-700 bg-indigo-100 hover:bg-indigo-200">
                              <font-awesome-icon :icon="['fas', 'plus']" class="mr-2" />
                              เพิ่มคุณสมบัติ
                            </button>
                          </div>
                          
                          <div v-for="(feature, index) in features" :key="index" class="flex items-center space-x-3 mb-3">
                            <input v-model="features[index]" type="text" placeholder="ระบุคุณสมบัติ"
                              class="flex-1 rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm" />
                            <button type="button" @click="removeFeature(index)"
                              class="text-red-600 hover:text-red-800">
                              <font-awesome-icon :icon="['fas', 'trash']" />
                            </button>
                          </div>
                        </div>

                      </div>
                      <div class="bg-gray-50 px-4 py-3 text-right sm:px-6">
                        <button type="submit"
                          class="inline-flex justify-center rounded-md border border-transparent bg-indigo-600 py-2 px-4 text-sm font-medium text-white shadow-sm hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2">
                          อัพเดท Package
                        </button>
                      </div>
                    </div>
                  </section>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </main>
  </template>
</template>

<style scoped>
.isblock::before {
  content: attr(data-content);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.9);
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  font-weight: bold;
  color: #374151;
}

.isblock {
  position: relative;
  pointer-events: none;
  opacity: 0.6;
}

.isunblock {
  position: relative;
  pointer-events: auto;
  opacity: 1;
}
</style>
